.menuList {
    height: 100%;
    color: white;
    width: 400px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menuList a {
    font-size: 20px;
    font-weight: 900;
    color: #f1cb3f;
    text-transform: uppercase;
    text-decoration: none;
}